.priduce{
	overflow: hidden;
}
.produce-top{
	position: relative;
	padding-top: 60px;
	z-index: 1;

	h2{
		font-size: 40px;
		text-align: left;
	}
}
.produce-slider{
	position: relative;
	margin-top: -135px;
	z-index: 0;
}
.produce-slide{
	position: relative;
}
.produce-slide__bg{
	position: absolute;
	width: 100%;
	height: 100%;
	background-size: cover;
	background-position: center;
	z-index: 0;
}
.produce-slide__content{
	position: relative;
	padding-top: 245px;
	padding-bottom: 300px;
	z-index: 1;
}
.produce__slider-thumbs{
	position: relative;
	img{
		// width: 16.66666666666667%;
	}
}
div.scrollWrapper{
	margin-top: -2px;
}
div.scrollableArea{
	margin: 0 auto;
	overflow: hidden;

	div{
		float: left;

		img{
			display: block;
		}
	}
}
div.scrollingHotSpotRight{
	background-image: url(../img/divscroll/big_transparent.gif);
	cursor: url(../img/divscroll/cursors/cursor_arrow_right.png), url(../img/divscroll/cursors/cursor_arrow_right.cur), e-resize;
}
div.scrollingHotSpotLeft{
	background-image: url(../img/divscroll/big_transparent.gif);
	cursor: url(../img/divscroll/cursors/cursor_arrow_left.png), url(../img/divscroll/cursors/cursor_arrow_left.cur), e-resize;
}
div.scrollingHotSpotRightVisible{
	background-image: url(../img/divscroll/arrow_right.gif);
}
div.scrollingHotSpotLeftVisible{
	background-image: url(../img/divscroll/arrow_left.gif);
}
.produce-slogans{
	position: absolute;
	width: 100%;
	left: 0;
	top: 150px;
	li{
		position: absolute;
		width: 100%;
		// left: 30px;
		top: 0;
		font-size: 100px;
		color: #fff;
		list-style-type: none;

		img{
			max-width: 100%;
		}
	}
}