.free-part{
	padding-top: 60px;
	background-color: #F9F8F8;
	.container{

	justify-content: space-between;
	}
	.title-wrapper{
		margin-bottom: 25px;
		p{
			color: #000;
			span:not(.paragraph){
				display: inline-block;
				font-family: $rob-b;
			}
			span.paragraph{
				font-family: $rob-l;
			}

		}
	}
}


.free-part__left,
.free-part__right{
	@include col();
	@include col-6();
	@include col-md-12();
}
.free-part__left{
	padding-top: 40px;
	img{
		margin-left: -50px;
		max-width: 110%;
	}

}
.free-part__right{

	// padding-top: 50px;
	

	p{
		font-size: 12px;
		line-height: 17px;
		margin: 0;
		margin-bottom: 5px;
		letter-spacing: -.3px;
		// font-family: $rob-th;
		
		a{
			color: #000;
		}
	}
}
.free-part__form{
	margin-bottom: 40px;
	padding: 48px 56px;
	background-color: #F9F8F8;
	box-shadow: 2px 2px 13px 3px rgba(0,0,0,0.2);
	border-radius: 10px;
	
	.button{
		width: 100%;
		margin-bottom: 25px;
		// background-color: transparent;
	}
}
form{
	label,input{
		display: block;
		width: 100%;
	}
	label{
		position: relative;
		margin-bottom: 48px;
		&:before{
			content: '';
			display: block;
			@include absolute-center;
			right: auto;
			left: 22px;
		}
		&.inp-user:before{
			@include sprite($user);
		}
		&.inp-phone:before{
			@include sprite($phone);
		}
		&.inp-mail:before{
				@include sprite($mail);
			}
		&.inp-time:before{
				@include sprite($clock);
			}

		
		&.invalid{
			border: 1px solid red;

		}

	}
	input{
		padding: 20px 25px 20px 63px;
		box-shadow: inset 0px 2px 5px 0px rgba(0, 0, 0, .3);
		// background-color: transparent;
		border: none;
		border-radius: 3px;
		font-family: $rob-r;
		font-size: 12px;
		color: #000;
		// @include placecolor(#000);

		&:focus::-webkit-input-placeholder {
		  	color: transparent !important	;
		  }

		  &:focus::-moz-placeholder {
		  	color: transparent !important	;
		  }
		  &:focus:-moz-placeholder {
		  	color: transparent !important	;
		  }
		  &:focus:-ms-input-placeholder {
		  	color: transparent !important	;
		  }
	}
	.button{
		
		background-color: transparent;
	}
}