.assortment{
	position: relative;

	.container{
		position: relative;
		top: -69px;
	}
	&-cover{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 58%;
	    background: linear-gradient(to bottom, #01062f 0%,#272d59 100%);
	}
}
.assortment-offset{
	position: relative;
	left: -7px;
	margin-top: 3.5px;
	transition: transform .25s ease-out;
}
.assortment-popup{
	position: absolute;
	bottom: 104%;
	left: 40%;
	width: 413px;
	padding: 15px 15px 20px 25px;
	background-color: $text-blue;
	box-shadow: 2px 1px 15px 1px rgba(0,0,0,0.65);
	z-index: 99;
	visibility: hidden;
	opacity: 0;
	transition: all .25s;
	transition-delay: .2s;
	transform: translateY(-100px);

	&:before{
		content: '';
		display: block;
		position: absolute;
		left: 5px;
		bottom: -12px;
		width: 24px;
		height: 24px;
		background-color: $text-blue;
		transform: rotate(45deg);
	}

}
.assortment-popup__top{
	text-align: left;
	font-family: $rob-b;
	color: #fff;
	line-height: 17px;
	margin-bottom: 15px;
}
.assortment-popup__bottom{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;

	text-align: right;

	ul{
		margin-left: 15px;
		li{
			padding-left: 5px;
			position: relative;
			margin-bottom: 12px;
			text-align: left;
			list-style-type: none;
			font-size: 12px;
			line-height: 16px;
			color: #fff;

			&:before{
				content: '';
				display: block;
				position: absolute;
				left: -7px;
				top: 5px;
				width: 4px;
				height: 4px;
				border-radius: 50%;
				background-color: #fff;
			}

		}
	}
}
.assortment-popup__button{
	display: inline-block;
	padding: 5px 15px;
	border-radius: 10px;
	font-size: 12px;
	text-align: center;
	color: rgba(#fff, .7);
	border: 1px solid rgba(#fff, .7);
	text-decoration: none;

	&:hover, &:active{
		background-color: #fff;
		color: $text-blue;
	}
	
}
.assortment-popup__profile{
	flex-shrink: 0;
	img{
		display: block;
	}
}
.assortment-item{
	@include col();
	@include col-4();
	@include col-lg-6();
	@include col-sm-12();
	position: relative;
	margin-bottom: 67px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	text-align: center;

	&:nth-child(2){
		.assortment-offset{
			left: -19px;
			margin-top: 4px;
		}
	}

	&:nth-child(3){
		.assortment-offset{
			left: -16px;
			margin-top: 2px;
		}
		.assortment-popup{
			left: auto;
			right: 30px;

			&:before{
				left: auto;
				right: 5px;
			}
		}
	}
	&:nth-child(4){
		.assortment-offset{
			left: -24px;
			margin-top: 1px;
		}
	}
	&:nth-child(5){
		.assortment-offset{
			left: -24px;
			margin-top: 2px;
		}
	}
	&:nth-child(6){
		.assortment-offset{
			left: -20px;
			margin-top: 3px;
		}
		.assortment-popup{
			left: auto;
			right: 30px;

			&:before{
				left: auto;
				right: 5px;
			}
		}
	}

	&:hover{
		z-index: 99;
		.assortment-popup{
			visibility: visible;
			transform: translateY(0);
			opacity: 1;
		}
	}

}
.assortment-visual{
	margin-bottom: 30px;
	text-align: center;
	img{
		max-width: 100%;
	}
}
.assortment-visual__shift{

	img{
		display: inline-block;
		vertical-align: middle;
	}
	&:hover{
		.assortment-offset{
			transform: translate3d(20px, -30px, 0);
			z-index: 1;
		}
		
	}
}

.assortment-title{
	margin-top: auto;
	font-size: 24px;
	font-family: $be-b;
	color: #fff;
	text-align: center;
	text-decoration: none;


	&--inverse{
		color: #05092d;
		// color: $text-blue;
	}
}
.statistic{
	margin-top: -69px;
	border-bottom: 1px solid #D1D1D1;
	border-top: 1px solid #D1D1D1;
	background-color: #fff;
	visibility: hidden;
	transition: all .2s;

	&-fixed{
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		margin: 0 auto;
		visibility: visible;
		z-index: 100;
	}
}
.statistic-inner{
	width: 100%;
	max-width: 1250px;
	margin: 0 auto;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	// align-items: center;
	.button-empty{
		align-self: center;
		margin-left: auto;
	}
}
.statistic-logo{
	cursor: pointer;
	div{
		vertical-align: middle;
		display: inline-block;
	}
}
.statistic-logo__img{
	width: 79px;
	height: 55px;
	background:  url(../img/logo-top.png) 0 center no-repeat;
	background-size: 100%;
}
.statistic-logo__text{
	font-family: $rob-r;
	color: #383838;
	margin-left: 10px;

	span{
		display: block;
		font-size: 12px;
		font-family: $rob-l;
		color: #999999;
	}
}
.statistic-item{
	position: relative;
	padding: 15px 65px 12px 65px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-direction: column;
	justify-content: center;

	border-right: 1px solid #D1D1D1;

	text-align: center;
	z-index: 1;

	&:hover, &:active{
		.statistic-hover{
			transform: translateY(0);
			opacity: 1;
		}
	}

	&--buttons{
		flex-direction: row;
		align-items: flex-start;
		border-right: 0 none;
		padding-left: 0;
		padding-right: 0;
	}
	
}
.statistic-item__cover{
	position: absolute;
	background-color: #fff;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
}
.statistic-logo{
	display: block;
	padding-left: 0;
}
.statistic-caption{
	position: relative;
	font-size: 13px;
	color: #9f9f9f;
	margin-bottom: 10px;
	z-index: 2;
}
.statistic-value{
	position: relative;
	font-size: 16px;
	font-family: $st;
	z-index: 2;
}

.statistic-val{
	margin-right: 5px;
	transition: color .5s ease;
}
.statistic-hover{
	position: absolute;
	padding: 19px 34px;
	top: 100%;
	width: 100%;
	left: 0;
	background-color: #fff;
	text-align: left;
	border: 1px solid #C2C2C2;
	// border-top: 1px solid #C2C2C2;
	// border-bottom: 1px solid #C2C2C2;
	transition: all .5s;
	transform: translateY(-100%);
	opacity: 0;
	z-index: 0;
}
.statistic-item--warehouse{
	.statistic-hover{
		width: calc(100% + 1px);
	// border-right: 1px solid #C2C2C2;
	left: -1px;

	}
}
.statistic-hover__title{
	margin-bottom: 12px;
	font-family: $rob-b;
	font-size: 11px;
	color: $text-blue;
}
.statistic-hover__body{
	font-size: 12px;
	color: #4f4f4f;
	font-family: $rob-r;
	line-height: 17px;

	span{
		font-family: $rob-b;
		color: #000;
	}
}
.statistic-button{
	position: relative;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	width: 209px;
	margin-left: 15px;
	padding-top: 3px;
	padding-bottom: 3px;
	padding-left: 15px;
	background: #EBEBEB;
	text-decoration: none;
	border-radius: 5px;
	box-shadow:         0px 5px 5px 0px rgba(0, 0, 0, 0.32);

	.statistic-button__text{
		padding: 12px 9px 12px 0;
		flex-grow: 1;
		color: #151515;
		font-size: 12px;
		border-right: 1px solid #C4C4C4;
	}

	&:hover{
		background-color: #F6F6F6;
	}
}
.statistic-button__popup{
	position: absolute;
	display: none;
	width: 100%;
	left: 0;
	top: 100%;
	

	&-inner{
		position: relative;
		display: block;
		margin-top: 23px;
		padding: 15px 12px;
	padding-top: 20px;
	background-color: $text-blue;
	color: #fff;
	text-align: left;
	font-size: 12px;
	line-height: 18px;
		
		&:before{
			content: '';
			display: block;
			position: absolute;
			width: 30px;
			height: 30px;
			background-color: $text-blue;
			right: 6px;
			top: -13px;
			transform: rotate(45deg);
		}
	}


}
.statistic-button__close{
	position: absolute;
	@include sprite($favor-close);
	top: 10px;
	right: 10px;
	cursor: pointer;
}
.statistic-button__icon{
	display: block;
	margin-left: 7px;
	margin-right: 9px;
	flex-shrink: 0;
}
.icon-favorites{
	@include sprite($to-favorites);
}
.icon-down-cat{
	@include sprite($download-doc);
}
.assortment-note{
	justify-content: center;
	p{
		@include col();
		@include col-10();
		margin-top: -30px;
		font-family: $rob-r;
		color: #6b6b6b;
	}
}