.map{
	padding-top: 84px;
	padding-bottom: 62px;
	.title-wrapper{
		p{
			color: #000;
		}
	}
}
.map-wrapper{
	width: 100%;
	position: relative;
	max-width: 1200px;
	// flex-shrink: 0;
	margin: 0 auto;
	margin-top: 50px;	

	img{
		max-width: 100%;
	}
}
.map-item{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: flex-end;
	flex-wrap: wrap;
	position: absolute;
	top: 0;
	left: 0;
	font-family: $rob-r;

	&:nth-of-type(1){
		    top: 6%;
    left: 5.5%;
    	.map-item__arrow{
    		@include sprite($line-kaliningrad);
    		top: -44%;

    	}
	}
	&:nth-of-type(2){
		    top: 1%;
			left: 25%;
		.map-item__arrow{
			@include sprite($line-murmansk);
			top: 100%;
			right: 100%;	
    	}
	}
	&:nth-of-type(3){
		  left: 3.5%;
    top: 45%;
		.map-item__arrow{
			@include sprite($line-anapa);
			bottom: 0;
			left: -17px;		
    	}
	}
	&:nth-of-type(4){
		  left: 5%;
    top: 40%;
    	.map-item__arrow{
			@include sprite($line-krasnodar);
			bottom: 0;
			left: -8px;		
    	}
	}
	&:nth-of-type(5){
		 top: 42%;
    left: 13%;
   	 	.map-item__arrow{
			@include sprite($line-saratov);
			bottom: 0;
			left: 0;		
    	}
	}
	&:nth-of-type(6){
		    top: 35%;
    left: 14%;
    	.map-item__arrow{
			@include sprite($line-cheboksary);
			bottom: 0;
			left: -15px;		
    	}
	}
	&:nth-of-type(7){
	top: 40%;
    left: 17%;
    	.map-item__arrow{
			@include sprite($line-kazan);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(8){
		    top: 48%;
    left: 18%;
    	.map-item__arrow{
			@include sprite($line-orenburg);
			bottom: 0;
			right: calc(100% - 9px);		
    	}
	}
	&:nth-of-type(9){
		    top: 45%;
    left: 21%;
   	 .map-item__arrow{
			@include sprite($line-ufa);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(10){
   	top: 22%;
    left: 29%;
    	.map-item__arrow{
			@include sprite($line-pechora);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(11){
    top: 39%;
    left: 27%;
    	.map-item__arrow{
			@include sprite($line-ekat);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(12){
            top: 45%;
    left: 29%;
    	.map-item__arrow{
			@include sprite($line-tyumen);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(13){
       top: 26%;
    left: 37%;
    	.map-item__arrow{
			@include sprite($line-labytnangi);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(14){
    top: 56%;
    left: 34%;
    	.map-item__arrow{
			@include sprite($line-omsk);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(15){
    top: 60%;
    left: 41%;
    	.map-item__arrow{
			@include sprite($line-novosibirsk);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(16){
        top: 47%;
    left: 43%;
    	.map-item__arrow{
			@include sprite($line-kemerovo);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(17){
        top: 72%;
    left: 52%;
    	.map-item__arrow{
			@include sprite($line-irkutsk);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(18){
        top: 77%;
    left: 60%;
    	.map-item__arrow{
			@include sprite($line-chita);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(19){
    top: 91%;
    left: 77%;
    	.map-item__arrow{
			@include sprite($line-vladivostok);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(20){
        top: 85%;
    left: 78%;
    	.map-item__arrow{
			@include sprite($line-usuri);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(21){
    top: 76%;
    left: 78%;
    	.map-item__arrow{
			@include sprite($line-khabarovsk);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(22){
        top: 48%;
    left: 84%;

    	.map-item__arrow{
			@include sprite($line-magadan);
			bottom: 0;
			right: 100%;		
    	}

	}
	&:nth-of-type(23){
        top: 80%;
    left: 84.5%;
    	.map-item__arrow{
			@include sprite($line-u-sahalinsk);
			bottom: 0;
			right: 100%;		
    	}
	}
	&:nth-of-type(24){
		top: 51%;
		left: 92%;
		flex-wrap: initial;
		align-items: flex-start;

		.map-item__arrow{
			@include sprite($line-petropavlovsk);
			bottom: 40%;
			right: 100%;		
    	}

		.map-item__name{
			// display: block;
    	margin-top: 15px;
   		}

	}

	&:nth-of-type(25){
		top: 43%;
		left: 72%;
		flex-wrap: initial;
		align-items: flex-start;

		.map-item__arrow{
			@include sprite($line-yakutsk);
			bottom: 40%;
			right: 100%;		
    	}

		.map-item__name{
			// display: block;
    	margin-top: 15px;
   		}

	}
    &.active{
    	.map-item__name,
    	.map-item__info{
    		color: #156505;
    		text-decoration: none;
    	}

    	.map-item__info{
    		display: block;
    		z-index: 3;
    	}

    	.map-item__flag{
    		@include sprite($flag-active);
    	}

    	.map-item__arrow{
    		display: block;
    	}
    }

}
.map-item__flag{
	// width: 22px;
	// height: 29px;
	@include sprite($flag);
	flex-shrink: 0;
	// background:  url(../img/icons-svg/flag.svg) 0 0 no-repeat;
	// background-size: 100%;
}
.map-item__name,
.map-plant--msc a{
	position: relative;
	// left: -5px;
	color: #071356;
	font-family: $rob-r;
	font-size: 11px;
	z-index: 3;
}
.map-info{
	@include col();
	@include col-4();
	@include col-md-6();
	@include col-sm-10();
	@include col-xs-12();
	position: relative;
	margin-top: -100px;
	color: #000;

	p{
		margin-top: 0;
		margin-bottom: 5px;
	}
}
.map-info__tel{
	margin: 20px 0;
	span{
		&:nth-child(2){
			margin-left: 5px;
			font-family: $rob-b;
			font-size: 16px;
		}
	}
}
.map-info__note {
	font-style: italic;
	font-size: 12px;	
	color: rgba(#000, .7);
}
.map-plant{
	
	position: absolute;

	color: #000;

	p{
		display: inline-block;
		vertical-align: middle;
		margin: 0;
		font-size: 12px;
		line-height: 14px;
		text-align: right;
		font-family: $rob-r;
	}
	img{
		margin-left: 15px;
		margin-right: 15px;
		vertical-align: middle;
	}
	&--msc{
		left: 8%;
		top: 25%;
		align-items: center;

		.map-item__flag{
			position: absolute;
			left: 100px;
			top: -1px;
		}

		.map-item__info{
			left: 100px;
			margin-left: 0;
			bottom: 0;
			top: auto;
		}
	}
	&--rostov{
		    top: 34%;
    left: 5%;

	    p{
	    	max-width: 100px;
	    	position: absolute;
	    	left: -90px;
	    	top: -15px;
	    }
	}
}
.map-plant--spb{
		top: 14%;
    left: 1%;

}
.map-item__info{
	width: 150px;
	margin-left: 23px;
	background-color: #fff;
	display: none;
	position: absolute;
	top: 100%;
	font-size: 11px;
	z-index: 0;
}
.map-item__arrow{
	display: none;
	position: absolute;
}