.clients{
	position: relative;
	h2{
		text-align: left;
	}
}
.clients-slide{
	position: relative;
}
.clients-slide__bg{
	position: absolute;
	width: 100%;
	height: 100%;
	background:  center top no-repeat;
	background-size: cover;
	z-index: 0;
}
.clients-slide__content{
	position: relative;
	padding-top: 57px;
	padding-bottom: 181px;
	min-height: 645px;
	z-index: 1;
	color: #fff;

	.title-wrapper{
		@include col();
		@include col-11();
		@include col-lg-12();
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center;
		margin-bottom: 33px;
	}
}
.clients-slide__logo{
	flex-shrink: 0;
	max-width: 128px;
	margin-right: 25px;

	img{
		max-width: 100%;
	}
}
.clients-slide__overlay{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(#000, 0.37);
}
.clients-slide__info{
	@include col();
	@include col-5();
	@include col-lg-12();

	p{
		margin-bottom: 38px;
		margin-top: 0;
		line-height: 18px;
	}
}
.clients-nav{
	@include 	absolute-center;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;	
	justify-content: center;
	top: 	auto;
	bottom: 90px;
	z-index: 2;
}
.clients-nav__item{
	width: 168px;
	height: 77px;
	margin: 0 1.5px;
	position: relative;
	background-color: #898989;
	cursor: pointer;
	transition: background-color .4s;

	img{
		@include absolute-center;
		max-height: 90%;
	}

	&:hover, &:active, &.active{
		background-color: $text-blue;
	}


	
}