.mission{
	padding-top: 134px;
	background-color: #ECECEC;
}
.mission-left{
	@include col();
	@include col-8();
	position: relative;
	z-index: 1;
}
.mission-right{
	@include col();
	@include col-4();
	position: relative;
	z-index: 0;

	img{
		max-width: 130%;
		position: relative;
		left: -70px;
		bottom: -5px;
	}
}


.mission-chief{
	padding-left: 62px;

	p{
		margin-top: 10px;
		color: #797979;
		span{
			display: block;
		}
	}

	header{
		position: relative;
		font-family: $rob-b;
		font-size: 16px;
		color: #636363;
		text-transform: uppercase;

		&:before{
			content: '';
			display: block;
			position: absolute;
			width: 26px;
			height: 3px;
			left: -50px;
			top: 8px;
			background-color: #0A397C;
		}

	}
}