/*==========  Desktop First Method  ==========*/

 

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px){
		.assortment-thumb{
			width: 326px;	
		}
		.colors{
			display: none;
		}
		.statistic{
			display: none;
		}
		.mission{
			display: none;
		}
		.kvis-item__form{
			display: none;
		}
		.map-item:not(.map-plant--msc){
			display: none;
		}
		.map-plant--spb,
		.map-plant--rostov{
			display: none;
		}
		.map-info{
			margin-top: 0;

		}

}
@media only screen and (max-width : 1100px){
	.subscribe-block__form{
		label{
			margin-bottom: 20px;
		}
	}
	.subscribe-block__form-bottom{
		margin-top: 30px;
	}
	.assortment-page-visual{
		max-width: 60%;	
		right: 0;
		bottom: -13px;		
	}
	.assortment-pointer{
		left: 	auto;
			right: 20%;	
	}
	.assortment-item__body{
		flex-wrap: wrap;
		height: auto;
	}
	.assortment-page-item__body{
		flex-wrap: wrap;
		height: auto;
	}
	.assortment-usage{
		flex-basis: 1;
		width: 70%;	
		border-right: 	1px solid #B1B1B1;

	}
	.assortment-thumb{
		height: 309px;
		flex-shrink: 0;
		width: 30%;		
	}
	.assortment__table-wrap{
			width: 100%;	
			margin-top: 10px;	
					table{
						position: relative;	
						left: 1px;	
						width: calc(100% - 1px);	
						th{
							border-top: 	1px solid #B1B1B1;
						}
					}
	}

	.assortment-cover{
		height: 70%;
	}
	.assortment-item{
		&:nth-of-type(4){
			.assortment-title--inverse{
				color: #fff;
			}
		}
	}
	.assortment-popup{
		left: 0;
		right: 0;
		bottom: 106%;
		margin: auto;

		&:before{
			left: 0;
			right: 0;
			margin: auto;
		}
	}
	.assortment-item:nth-child(6),
	.assortment-item:nth-child(3){
		.assortment-popup{
			left: 0;
			right: 0;
			margin: auto;

			&:before{
				left: 0;
				right: 0;
				margin: auto;
			}
			
		}
	}

	.quality-item:first-child{
		top: 0;
	}
	.quality-item:nth-child(2){
		top: 0;
	}
	.quality-sertification{
		margin-top: 0;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;

		.quality-sertification__top, .button, p{
			width: calc(25% - 30px);
			margin: 0 15px;
			// flex-shrink: 0;
			// flex-grow: 1;
		}

		p{
			text-align: left;
		}
	}
	.quality-sertification__top{
		img{
			max-width: 100%;
		}
	}
	.quality-illustration{
		display: none;
	}

	.clients-slide__content{
		height: 480px;
	}
	.clients h2{
		font-size: 35px;
		line-height: 40px;
	}

	.assortment__table-wrap table tr td:first-of-type,
	.assortment__table-wrap table tr th:first-of-type{
		border-left: 1px solid #B1B1B1;
	}
}
	
/* Medium Devices, Desktops */
@media only screen and (max-width : 992px){
	.assortment-thumb{
		width: 50%;		
	}
	.assortment-usage{
		width: 50%;	
		max-height: 309px;
	}
	.header-top{
		nav{
			display: none;
		}
	}
	.assortment-visual__shift{
		&:hover{
			.assortment-offset{
				transform: translate3d(0, 0, 0);
				// z-index: 0;
			}

		}
	}
	.free-part__left{
		img{
			max-width: 100%;
		}
	}
	.recomendations__form{
		label, .button{
			margin-bottom: 10px;
			
		}

	}
	
}
	
@media only screen and (max-width : 960px){
	.free-part__left{
		padding-top: 0;
		img{
			
		margin-left: 0;
		}
	}
	.kvis{
		min-height: initial;
	}
	.kvis-wrapper{
		min-height: 450px;
	}
	.kvis h2 {

    margin-bottom: 18px;

}
	.kvis-item__ques{
		height: 50px;
		font-size: 12px;
		padding: 10px;
		margin-top: 10px;
		margin-bottom: 10px;
	}
	.kvis-dynamics{
		bottom: 30px;
	}
	.kviz-form .button{
		margin-top: 15px;
	}

	
	.recomendations h2{
		margin-bottom: 30px;
	}
	// .recomendations__form .button{
	// 	margin-left: 0;
	// }
	.quality-sertification{


		.quality-sertification__top, .button, p{
			width: calc(33.33333% - 30px);

		}


	}

	.contacts-phone{
		margin-bottom: 15px;
	}

	.contacts-calls{
		flex-direction: column;
		.call-order{
			margin-bottom: 15px;
		}
		.button-empty{
			margin-left: 0;
		}
	}

}

