	} 
}	
	

/* Extra Small Devices, Phones */
@media only screen and (max-width : 560px){
	.manual-bottom__item{
			margin-bottom: 45px;
	}
	.manual-bottom__pict{
		margin-bottom: 15px;
	}
	.manual-bottom__text{
		text-align: center;

		div{
			margin-bottom: 10px;
		}

		p{
			margin-top: 0;
			margin-left: auto;
			margin-right: auto;
		}
	}
	.subscribe-block__form{
		label, .button{
			width: 90%;
		}
	}
	.title-wrapper--subscribe{
		margin-bottom: 0;
	}
	.subscribe-block__inner{
		margin-bottom: 30px;
	}
	.popup{
		padding-left: 15px;
		padding-right: 15px;
	}
	.assortment-thumb,
	.assortment-usage{

		width: 100%;		
	}
	.assortment-usage{
		border-left: 	1px solid #B1B1B1;
	}

	.header-top{
		padding-top: 5px;
		.container{
			flex-direction: column;
			align-items: center;
		}
	}
	.header-logo{
		margin-bottom: 10px;

		img{
			max-width: 100px;
		}
	}
	.header-contacts{
		.header-phones{
			display: block;
			margin-right: 0;
			margin-bottom: 10px;
		}
		.header-mail{
			display: block;
		}
		
	}

	h1, h2{
		font-size: 29px;
		line-height: 34px;
	}

	.button .button__inner{
		padding: 11px 20px;
		font-size: 12px;
	}
	.button-styled{
		.button-submit__inner{
			padding: 18px 20px;
			font-size: 12px;

		}
	}
	.button-submit .button-submit__text{
		font-size: 12px;
		line-height: 33px;
	}
	.kvis-wrapper{
		min-height: 405px;
	}
	.kvis-title{
		font-size: 15px;
		text-align: center;
	}
	.kvis-item__ques{
		width: 100%;
		height: 30px;

		margin: 7px 0;
	}
	.kviz-progress{
		display: none;
	}
	.kvis-bottom{
		margin-bottom: 0;
	}
	.recomendations__form label{
		margin-left: 0;
		margin-right: 0;
		
	}

	.produce-slide__content{
		padding-top: 150px;
		padding-bottom: 200px;
	}
	.produce-slogans{
		top: 110px;
	}
	.produce-top h2{
		font-size: 32px;
		line-height: 35px;
	}
	div.scrollableArea div img{
		max-width: 150px;
	}
	.quality .title-wrapper{
		flex-direction: column;
	}
	.quality-logo{
		height: 92px;
		align-self: flex-end;
	}
	.quality-item__title{
		font-size: 18px;
	}
	.contacts{
		.section-label{
			margin-bottom: 25px;
		}
	}
	.section-slogan{
		font-size: 24px;
		line-height: 28px;	
	}

	.assortment-usage__top{
		border-top: 1px solid #B1B1B1;
	}

	.header-bottom .title-wrapper--assortment .assortment-note{
		font-size: 10px;	
		line-height: 12px;	
	}
	.recomendations__form{
		flex-direction: column;
	}

}

@media only screen and (max-width : 480px){
	.subscribe-block__form-bottom{
		
		label{
			width: 90%;
			margin-left: auto;
			margin-right: auto;
			display: -webkit-flex;
			display: -moz-flex;
			display: -ms-flex;
			display: -o-flex;
			display: flex;
			text-align: left;
			overflow: hidden;

			// span{
			// 	padding-left: 30px;
			// 	font-size: 12px;
			// } 
		}
	} 
	.checkbox-custom{
		flex-shrink: 0;
	}
	.popup-manager__portrait{
		width: 290px;
		height: 290px;	
	}
	.form-bottom{
		label{
			overflow: hidden;	

			span{
				float: 	left;
				margin-right: 0;	
			}
			
			span:not(.checkbox-custom){
				margin-left: 30px;
				text-align: 	left	;	
				margin-top: -20px;		
			}
		}
	}
	.header-button-popup{
	.form-bottom{
				label{
					margin-left: 0;	
				}
			}
		
	}
	.assortment__table-wrap{
		overflow: scroll;	
		table{
			width: 480px;	
		}
	}

	.assortment-cover {

		height: 83%;

	}
	.assortment-item{
		margin-bottom: 60px;
	}
	.assortment-visual{
		margin-bottom: 17px;
	}
	.assortment-popup{
		width: 95%;
	}
	.assortment-visual__shift{
		img{
		max-height: 118px;
			
		}
		.assortment-offset{
			max-height: 126px;
		}
	}
	.assortment-item:nth-child(2) .assortment-offset{
		margin-top: 2px;
	}
	.assortment-item:nth-child(3) .assortment-offset{
		left: -12px;
		max-height: 125px;
	}
	.assortment-item:nth-child(4) .assortment-offset{
		max-height: 125px;
	}
	.assortment-item:nth-child(5) .assortment-offset{
		max-height: 125px;
	}
	.assortment-item:nth-child(6) .assortment-offset{
		left: -9px;
		max-height: 124px;
	}

	.free-part__form{
		padding: 15px;
	}
	.free-part__left{
		img{
			margin-left: -20px;
		}
	}
	form{
		label{
			margin-bottom: 15px ;
		}
	}

	.kviz-counter__parts,
	.kviz-counter__persents,
	.kviz-handlers{
		font-size: 12px;
	}
	.kvis-bottom__divider{
		margin: 0 10px;
	}
	.clients h2{
		font-size: 24px;
		line-height: 28px;
	}
	.clients-slide__info p{
		font-size: 12px;
		line-height: 16px;
	}
	.clients-slide__logo{
		max-width: 64px;
	}
	.clients-nav__item{
		width: 50%;
		margin: 0;
	}
	.clients-slide__content{
		min-height: 520px;
	}
	.clients-nav{
		bottom: 30px;
	}
	.clients-nav__item {

		// width: 100px;
		height: 60px;
	}
	.clients-slide__content .title-wrapper {
		margin-bottom: 10px;
	}
	.clients h2{
		font-size: 18px;
		line-height: 20px;
	}
	.clients-slide__info p{
		margin-bottom: 5px;
	}
	.assortment-visual{
		img{
			max-width: 60%;
		}
	}

}
		
