.button{
	display: inline-block;
		position: relative;
	text-decoration: none;

	cursor: pointer;

	
	.button__inner{
	display: block;
	position: relative;
	padding: 20px 32px;
	padding-bottom: 22px;
	text-align: center;
	border-radius: 10px;
	font-size: 16px;
	font-family: $rob-b;
	color: #fff;
	background: linear-gradient(to bottom, #f57d08 0%,#d70202 100%);
	z-index: 1;
	overflow: hidden;
	}

	&-submit{
		.button__inner{
			width: 100%;
			border: none;
		}
	}

	&-empty{
		position: relative;
		padding: 15px 37px 17px 64px;
		color: #727272;
		text-align: center;
		border: 1px solid #6986AF;
		border-radius: 5px;
		text-decoration: none;

		&:before{
			content: '';
			display: block;
			@include sprite($download);
			@include absolute-center;
			right: auto;
			left: 37px;
		}

	}

	&:after{
		content: '';
		display: block;
		width: 99%;
		height: 100%;
		position: absolute;
		background-color: #790000;
		border-radius: 10px;
		top: 5px;
		left: 0;
		right: 0;
		margin: auto;
		z-index: 0;
		box-shadow: 0px 4px 8px 0px rgba(81, 14, 1, 0.9);
	}
	&:hover{
		// &:after{
		// 	top: 2px;
		// }

		.button__inner{
			top: 2px;	
		}
	}
}



.button-styled{
	
	.button__inner{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: transparent;
		padding: 0;
		box-shadow: none;
		border: none;
		cursor: pointer;
		z-index: 10;
	}

	&-submit{
		.button__inner{
			width: 100%;
			border: none;
		}
	}

