.button-submit__inner{
	display: block;
	position: relative;
	padding: 39px 32px;
	padding-bottom: 22px;
	text-align: center;
	border-radius: 10px;
	font-size: 16px;
	font-family: $rob-b;
	color: #fff;
	background: linear-gradient(to bottom, #f57d08 0%,#d70202 100%);
	z-index: 1;
	overflow: hidden;

}
.button__effect{
	 position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background: white;
	z-index: 2;

  animation: button-animation 2s infinite;
}
@keyframes button-animation {
    from {
      transform: scale(1);
      opacity: 0.4;
    }
    to {
      transform: scale(100);
      opacity: 0;
    }
}
