.colors{
	padding-bottom: 62px;
	padding-top: 62px;	

	h2{
		// line-height: 47px;
		p{
			margin: 0;
			margin-top: 13px;
			color: #000;
			font-size: 32px;
			span{
				color: $text-blue;
			}
		}
	}
}
.colors-items{
	height: 682px;
	position: relative;
}
.colors__examples{
	position: absolute;
	width: 100px;
	height: 100%;
	left: 0;
	top: 0;
}
.colors-example__popup{

	width: 308px;
	position: absolute;
	visibility: hidden;
	padding: 20px 17px 15px 26px;
	    left: 30px;
    top: -230px;
	background: linear-gradient(to right, #05357a 0%,#10b7db 100%);
	box-shadow: 3px 5px 14px 0px rgba(0, 0, 0, 0.65);
	color: #fff;
	opacity: 0;
	transition: all .25s;
	transition-delay: 0.2s;
	/* box-sizing: border-box; */


	&:after{
		content: '';
		display: block;
		// @include sprite($colors-triangle);
		position: absolute;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 15px 8.5px 0 8.5px;
		border-color: #007bff transparent transparent; 
		// @include sprite($colors-triangle);
		// width: 37px;
		// height: 21px;
		// background: url(../img/icons/colors-triangle.png) 0 0 no-repeat;
		// left: -10px;
		// bottom: -31px;
		left: 0;
		top: 100%;
	}

	ul{
		li{
			position: relative;
			margin-left: 8px;
			list-style-type: none;


			&:before{
				content: '';
				display: block;
				position: absolute;
				top: 6px;
				left: -9px;
				width: 4px;
				height: 4px;
				border-radius: 50%;
				background-color: #FFDE00;
			}

		}
	}

	p{
		margin-bottom: 0;
		font-family: $rob-th;
	}

	&--constant{
		opacity: 1;
		visibility: visible;
		top: -165px;
		background: linear-gradient(to right, #383838 0%,#919191 100%); 
		&:after{
			// @include sprite($colors-triangle-gray);
			// background-image: url(../img/icons/colors-triangle-gray.png);
			border-color: #3A3A3A transparent transparent; 
		}

		
	}
}
.colors-example__title{
	margin-bottom: 14px;
	font-size: 20px;
	font-family: $be-r;
}
.colors-example{
	position: absolute;
	top: 0;
	left: 0;

	img{

		transition: margin-top .25s;
		transition-delay: .1s;
		
		
	}

	&:nth-child(1){
		.colors-example__popup{
			color: #000;

	       background: #EDEDED;

	       &:after{
	       		border-color: #EDEDED transparent transparent 
	       }
     	p{
     		// color: #000;
     		font-family: $rob-l;
     	}
		}
	}
	
	&:nth-child(2){
		left: 188px;
top: 83px;
		.colors-example__popup{

	       background: linear-gradient(to right, #BDB95C 100%,#000 100%);

	       &:after{
	       		border-color: #BDB95C transparent transparent 
	       }
		}
	}
	&:nth-child(3){
		top: 169px;
		left: 382px;

		.colors-example__popup{

	       background: linear-gradient(to right, #787366 100%,#000 100%);

	       &:after{
	       		border-color: #787366 transparent transparent 
	       }
		}
	}
	&:nth-child(4){
		top: 267px;
		left: 604px;

		.colors-example__popup{

	       background: linear-gradient(to right, #5E4B3A 100%,#000 100%);

	       &:after{
	       		border-color: #5E4B3A transparent transparent 
	       }
		}
	}
	&:nth-child(5){
     top: 372px;
left: 838px;
	}
	&:hover{
		img{

			margin-top: -30px;
		}
		.colors-example__popup{
			opacity: 1;
			top: -180px;
			visibility: visible;
			z-index: 1;
		}
	}
}
.colors-example--constant{
	// img{
	// 	margin-top: -30px;
	// }
		&:hover, &:active{
			img{
				margin-top: 0;
			}
			.colors-example__popup{

				top: -165px;

			}
		}
		
	// }
}
.colors-items__shadow{
	position: absolute;
	max-width: 100%;
	top: 91px;
left: -12px;
}
.colors-block{
	@include col();
	@include col-5();
	align-self: flex-end;

	p{
		margin-bottom: 18px;
		font-size: 16px;
		font-family: $rob-r;
		line-height: 19px;
		color: #727272;
	}
	.button{
		padding-left: 0;
		padding-right: 0;
		width: 293px;
	}
}
.colors-block__title{
	margin-bottom: 16px;
	font-family: $be-r;
	font-size: 35px;
	line-height: 37px;
}

.colors-tooltip{
	color: #fff;
	font-size: 12px;
	line-height: 18px;

	ul{
		li{
			padding-left: 30px;
			margin-left: 15px;
		}
	}
	p{
		margin: 0;
		margin-bottom: 14px;
	}
}
.powder-tip{
	height: auto !important;
	background-color: transparent;
	overflow: visible;
	.tooltipster-box{
	box-shadow: 1px 5px 15px 3px rgba(0, 0, 0, 0.4);
		margin-bottom: 0;
		padding: 20px 15px;
		background-color: $text-blue;
	}
	.tooltipster-content{
		padding: 0;
	}
}
.tooltipster-sidetip.tooltipster-top .tooltipster-box{
	margin-bottom: 0;
}


.powder-tip.tooltipster-sidetip .tooltipster-arrow{
	overflow: visible;
}
.powder-tip.tooltipster-sidetip.tooltipster-bottom .tooltipster-arrow-border{
	border-bottom-color: $text-blue;
	
}
