.recomendations{
	padding-top: 110px;
	padding-bottom: 43px;

	h2{
		margin-bottom: 60px;
		span{
			color: $text-blue;
		}
	}
}
.recomendations__form{
	@include col();
	@include col-12();
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	margin-bottom: 42px;

	label{
		width: 284px;
		margin-left: 9px;
		margin-bottom: 0;

		input{
			display: block;
			padding-top: 25px;
			padding-bottom: 25px;
			background-color: #EBEBEB;
		}
	}

	.button{
		margin-left: 27px;
	}
}
.recomendations-note{
	@include col();
	@include col-12();
	p{
		max-width: 680px;	
		margin: 0 auto;
		margin-bottom: 4px;
		text-align: center;
		font-size: 12px;
		text-align: 	left;
		
		a{
			color: #000;
		}
	}
}