.header{
	background-color: $bg-h;
}
.header-top{
	padding-top: 20px;
	.container{
		justify-content: space-between;
		align-items: center;
	}
	nav{
		a{
			font-size: 16px;
			margin-right: 27px;
			color: #5c5c5c;
			text-decoration: none;

			&:hover, &:active{
				color: #000;
			}
			
		}
	}
}
.header-logo{
	img{
		margin-right: 18px;
		vertical-align: middle;
	}
	span{
		display: inline-block;
		vertical-align: middle;
		line-height: 16px;
		font-family: $rob-r;
		color: #606060;
	}
}
.header__button-wrapper{
	position: relative;
	margin-top: 20px;
	display: inline-block;
	&:before{
			content: '';
			position: absolute;
			display: block;
			width: 124px;
			height: 64px;
			left: -140px;
			top: -30px;
			background:  url(../img/header-arrow.png) 0 0 no-repeat;
			background-size: 100%;
		}
}
.header-contacts{
	font-family: $rob-r;
	font-size: 16px;
	line-height: 18px;
	.header-phones, a{
		display: inline-block;
		vertical-align: middle;

		a{
			color: #5b5b5b;
		text-decoration: none;
		color: $text-blue;
		&:hover, &:active{
			color: #000;
		}
		
		}
	}
	.header-phones{
		position: relative;
		margin-right: 26px;
		color: #1048a0;

		&:before{
			content: '';
			display: block;
			@include absolute-center;
			left: -25px;
			right: auto;
			@include sprite($phone-blue);
		}

	}
	a{
		color: #5b5b5b;
		text-decoration: none;

	}
}
.header-mail{
	&:hover, &:active{
		color: #000;
	}
	
}
.header-bottom{
	padding-top: 58px;
	padding-bottom: 146px;
	h1{
		span{
			color: #05306d;
		}
	}
	.title-wrapper{
		p{
			margin-left: auto;
			margin-right: auto;
			max-width: 520px;
			margin-bottom: 15px;
			font-size: 16px;
			text-align: left;
			color: #000;
			font-family: $rob-r;

			span{
				display: block;
			}
		}
	}
	.button{
		
	}
}

.header-download-tooltip{
	p{
		color: #000;
		font-size: 13px;
		line-height: 20px;
	}

	a{
		position: relative;
		// padding-bottom: 7px;
		font-size: 14px;
		color: #1048a0;

		&:before{
			content: '';
			display: inline-block;
			margin-right: 7px;
			vertical-align: middle;
			@include sprite($download-blue);
		}

	}
}

.header-download-tip{
	.tooltipster-content{
		padding-bottom: 15px;
	}
}

.header-download-tip.tooltipster-sidetip.tooltipster-bottom .tooltipster-arrow-border{
	border-bottom-color: #fff;
	
}
.header-download-tip.tooltipster-sidetip .tooltipster-arrow-border{
	// border-width: 23px;
}
.header-download-tip.tooltipster-sidetip.tooltipster-left .tooltipster-arrow-border{
	border-left-color: #fff;
}
.header-download-tip.tooltipster-sidetip.tooltipster-top .tooltipster-arrow-border{
	border-top-color: #fff;
}
.header-download-tip.tooltipster-sidetip.tooltipster-left .tooltipster-arrow-background{
	display: none;
}
.header-download-tip.tooltipster-sidetip.tooltipster-top .tooltipster-arrow-background{
	display: none;
}
.header-download-tip.tooltipster-sidetip.tooltipster-right .tooltipster-arrow-border{
	border-right-color: #fff;
}
.header-download-tip.tooltipster-sidetip.tooltipster-right .tooltipster-arrow-background{
	display: none;
}
.header-download-tip.tooltipster-sidetip.tooltipster-right .tooltipster-arrow{
	// left: -23px;
}

.header-download-tip.tooltipster-bottom .tooltipster-arrow-background{
	display: none;
}
