.contacts{
	padding-top: 114px;
	padding-bottom: 120px;

	.section-slogan{
		margin-bottom: 35px;
	}

	&-inner{
		@include col();
		@include col-12();
	}
}
.contacts-adress__title{
	margin-bottom: 20px;
	font-family: $rob-r;
	font-size: 16px;
	color: $text-blue;
}
.contacts-adress{
	margin-bottom: 35px;
	p{
		margin-top: 0;
		font-size: 16px;
		span{
			font-style: italic;
		}
	}
}
.contacts-calls{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
}
.contacts-phone{
	a, span{
		display: block;
	}
	a{
		margin-bottom: 5px;
		font-family: $rob-r;
		font-size: 30px;
		color: $text-blue;
		text-decoration: none;
	}
	span{
		color: #4c4c4c;
	}
}
.button-empty{
	display: block;
	position: relative;
	padding-left: 5px;
	padding-right: 5px;
	width: 257px;
	margin-left: 35px;
	border-radius: 15px;

	border-left:none;
	

	&:before{
			display: block;
			left: 15px;
			@include sprite($vibrotel);
			z-index: 1;

		}
		&:after{
			content: '';
			display: block;
			position: absolute;
			width: 51px;
			height: 100%;
			left: 0;
			top: 0;
			background-color: $text-blue;
			border-radius: 15px 0 0 15px;
		}

	&--present{
		padding-left: 37px;
		overflow: hidden;
		&:before{
			@include sprite($disket);
		}
	}
	&:hover, &:active{
		&:before{
			animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;/*     ,   */
  transform: translate3d(0, 0, 0);
		}
	}
	
}
@keyframes shake {
  10%, 90% {/*     100  900 */
    transform: translate3d(-1px, 0, 0);  /*   -    ,   -   .*/
  }
  
  20%, 80% {/*     200  800 */
    transform: translate3d(2px, 0, 0); 
  }

  30%, 50%, 70% {/*     300, 500  700 */
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {/*     400  600 */
    transform: translate3d(4px, 0, 0); 
  }
  }