.kvis{
	padding-top: 68px;
	// padding-bottom: 73px;
	min-height: 614px;
	background: linear-gradient(to bottom, #262c59 0%,#02062f 100%);

	h2{
		margin-bottom: 65px;
	}
}
.kvis-wrapper{
	position: relative;	
	justify-content: center;
	min-height: 480px;
}
.kvis-body{
	@include col();
	@include col-10();
	@include col-xs-12();
	// margin-bottom: 64px;
}
.kvis-title{
	margin-bottom: 32px;
	color: #fff;
	font-size: 20px;
}
.kvis-item__variants{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	margin: 0 -17px;
	// justify-content: space-between;
}
.kvis-item__ques{
	width: calc(33.33333333333333% - 34px);
	padding: 20px 10px;
	height: 71px;
	margin: 17px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	text-align: center;
	justify-content: center;
	border: 1px solid #414568;
	font-size: 20px;
	text-transform: uppercase;
	color: #797C94;
	cursor: pointer;
	transition: all 0.3s;

	&:hover, &:active{
		color: #fff;
		border-color: #fff;
	}
	// &:active{
	// 	color: #797C94;
	// 	border-color: #414568;
	// }
	
}
.kvis-dynamics{
	 @include col();
	@include col-10();
	@include absolute-center;
	position: absolute;	
	top: 	auto;
	bottom: 	70px;
}
.kvis-bottom{
	// @include col();
	// @include col-10();
}
.kvis-bottom{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	margin-bottom: 28px;
	color: #fff;
	text-transform: uppercase;
}
.kvis-bottom__divider{
	width: 1px;
	height: 30px;
	margin: 0 20px;
	background-color: #84869B;
}
.kviz-handlers{
	color: #84869B;
}
.kviz-counter, .kviz-handlers{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	font-family: $rob-r;
}
.kviz-counter{
	font-size: 20px;
}
.kviz-prev,
.kviz-next{
	cursor: pointer;
	transition: color .3s;

	&:hover{
		color: #fff;
	}
	
}
.kviz-progress{
	// @include col();
	// @include col-10();
	position: relative;
	height: 9px;
	border: 1px solid #fff;
	border-radius: 5px;
}
.kviz-wrapper{
	@include absolute-center;
	width: 99%;
	height: 70%;
	border-radius: 5px;
	overflow: hidden;
}
.kviz-progress__inner{
	position: absolute;
	width: 5%;
	height: 100%;
	left: 0;
	top: 0;
	background-color: #fff;
	border-radius: 5px;
}
.ques-icon{
	width: 27px;
	height: 27px;
	display: inline-block;
	margin-left: 22px;
	font-size: 20px;
	font-family: $rob-r;
	color: #fff;
	border-radius: 50%;
	border: 1px solid #fff;
	line-height: 25px;
	text-align: center;
}
.kvis-item__form{
	margin-top: 40px;
	span{
		vertical-align: middle;
		margin-right: 22px;
			vertical-align: middle;
			font-family: $rob-r;
			font-size: 17px;
			color: #fff;

			
			
	}
	label{
		display: inline-block;
		width: auto;

		input[type="file"]{
			padding: 0;
			position: absolute;
			left: -9999px;
			box-shadow: inset none;
		}

		span{
			display: inline-block;

			width: 40px;
			height: 40px;
			position: relative;
			border: 1px solid #9EA0B1;
			border-radius: 50%;

			&:before{
				content: '';
				display: block;
				@include sprite($file);
				@include absolute-center;
			}

			&:hover, &:active{
				 opacity: .5;	
			}

		}
	}
}
.ques-icon-tip{
	height: auto !important;
	overflow: visible;
	.tooltipster-box{
		border-radius: 0;
		background-color: #05357A;
		margin-left: 14px;
		box-shadow: 1px 2px 11px 0px rgba(0, 0, 0, 0.5);
	}
	.tooltipster-content{
		padding: 10px 18px;
		line-height: 16px;
		font-size: 12px;
	}
}
.ques-icon-tip.tooltipster-sidetip .tooltipster-arrow{
	overflow: visible;
}
.ques-icon-tip.tooltipster-sidetip.tooltipster-top .tooltipster-arrow{
	height: inherit;
}
.ques-icon-tip.tooltipster-sidetip.tooltipster-bottom .tooltipster-arrow-border{
	border-bottom-color: $text-blue;
	
}
.ques-icon-tip.tooltipster-sidetip .tooltipster-arrow-border{
	// border-width: 23px;
}
.ques-icon-tip.tooltipster-sidetip.tooltipster-left .tooltipster-arrow-border{
	border-left-color: $text-blue;
}
.ques-icon-tip.tooltipster-sidetip.tooltipster-top .tooltipster-arrow-border{
	border-top-color: $text-blue;
}
.ques-icon-tip.tooltipster-sidetip.tooltipster-left .tooltipster-arrow-background{
	display: none;
}
.ques-icon-tip.tooltipster-sidetip.tooltipster-top .tooltipster-arrow-background{
	display: none;
}
.ques-icon-tip.tooltipster-sidetip.tooltipster-right .tooltipster-arrow-border{
	border-right-color: $text-blue;
}
.ques-icon-tip.tooltipster-sidetip.tooltipster-right .tooltipster-arrow-background{
	display: none;
}


.kviz-item-tip{
	margin: 0;
	padding: 0;
	overflow: visible;
	.tooltipster-box{
		margin: 0;
		padding: 0;
		background-color: transparent;
		box-shadow: none;
	}
	.tooltipster-content{
		padding: 0;
	}
}
.kviz-form{

	@include 	row-flex();
	@include 	wrapper();
	justify-content: center;	

	label{
		@include 	col();
		@include 	col-7();
		@include 	col-md-10();
		@include 	col-sm-12();
		margin-bottom: 10px;	
		input[type='text'], input[type="number"], input[type="datetime"]{
			border: none;
			border-bottom: 2px solid #8D8D8D;
			box-shadow: none;
			background-color: transparent;
			border-radius: 0;	
			color: #FFF;
			@include 	placecolor(#fff);
		}
		&.inp-user:before{
			@include sprite($user-blue);
		}
		&.inp-phone:before{
			@include sprite($phone-empty);
		}

	}

	.button{
		margin-top: 30px;	
		width: 100%;	
		max-width: 390px;
	}

	.form-bottom{
		@include col();
		@include col-12();
	}
	.checkbox-custom::before{
		@include sprite($cust-check-wt);
	}
}