.header--manual{
	background-color: transparent;
}
.header-bottom--manual{
	padding-bottom: 0;
}
.manual-top{
	padding-bottom: 42px;
	.container{
		justify-content: center;
	}
}
.manual-top__inner{
	@include col();
	@include col-10();
	@include col-xs-12();
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;

	padding: 14px 34px;
	color: #fff;

	background-color: #05357A;

	p{
		// margin-top: 0;
		font-size: 16px;

		span{
			display: block;
			font-family: $rob-b;
		}

	}
}
.manual-top__portrait{
	width: 125px;
	height: 125px;
	position: relative;
	margin-right: 34px;
	flex-shrink: 0;
	border-radius: 50%;
	overflow: hidden;
	text-align: center;

	img{
		@include absolute-center;
		top: 20px;
		left: 50%;
		transform: translateX(-50%);
	}
}
.manual-bottom{
	padding: 45px 0;
}
.manual-bottom__item{
	width: 100%;

	@include row-flex();
	@include wrapper();

	margin-bottom: 65px;

}
.manual-bottom__pict,
.manual-bottom__text{
	@include col();
	@include col-6();
	@include col-xs-12();
}
.manual-bottom__pict{
	text-align: center;

	img{
		max-width: 100%;
	}
}
.manual-bottom__text{
	div{
		margin-bottom: 35px;
		font-family: $be-b;
		color: $text-blue;
		font-size: 34px;
	}
	p{
		max-width: 230px;
		font-size: 22px;
		color: #000;
	}
}