.popup{
	padding: 40px 54px;
	margin: 	0 auto;
	position: relative;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	background-color: #F9F8F8;



}
.popup-header-phone{
	width: 100%;
	max-width: 982px;

	.popup-left{
		flex-shrink: 0;
		
	}
	.popup-right{
		p{
			margin-bottom: 40px;
		}
	}
}
.popup-manager{
	margin-right: 65px;
	text-align: center;

	p{
		span{
			display: block;
		}
	}
}
.popup-manager__portrait{
	width: 328px;
	height: 328px;
	margin: 0 auto;
	margin-bottom: 24px;
	position: relative;
	border-radius: 50%;
	overflow: hidden;

	img{
		@include absolute-center;
		left: 50%;
		transform: translateX(-50%);
	}
}
.popup-manager__name{
	font-family: $rob-r;
	font-size: 18px;
}
.popup-title{
	margin-bottom: 40px;
	font-family: $be-b;
	font-size: 27px;
	line-height: 28px;
}
.popup-list{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	margin-bottom: 16px;

	span{
		margin-right: 30px;
		font-size: 16px;
	}
}
.popup-header-phone__form{
	label{
		margin-bottom: 22px;
		input{
			@include placecolor(rgba(0, 0, 0, 0.7));
			&::focus::-webkit-input-placeholder {
				  	color: transparent !important	;
				  }

			  &:focus::-moz-placeholder {
			  	color: transparent !important	;
			  }
			  &:focus:-moz-placeholder {
			  	color: transparent !important	;
			  }
			  &:focus:-ms-input-placeholder {
			  	color: transparent !important	;
			  }
		}
	}
	.button{
		width: 100%;
	}
	.form-bottom{
		margin-top: 26px;	
	}
}
.header-button-popup{
	width: 100%;
	max-width: 585px;
	display: block;	
	padding-bottom: 19px;	
	text-align: center;	

		background-color: #032555;

		p{
			margin-bottom: 30px;	
			color: #cacaca;
			font-family: $rob-b;
			font-size: 14px;
			line-height: 19px;
			text-align: 	left;
			text-transform: uppercase;

			span{
				display: block;
			}
		}

		.popup-title{
			font-family: $rob-b;
			font-size: 18px;
			color: #fff;
			text-align: left;
			text-transform: uppercase;
		}

		.button{
			margin-top: 20px;	
			margin-bottom: 20px;	
		}

		.form-bottom{
			text-align: 	left;

			label{
				margin-left: -15px;	
			}
		}

		label{
			margin-bottom: 10px;	
			input[type='text'], input[type="number"]{
				  border: none;
				  border-bottom: 2px solid #8D8D8D;
				  box-shadow: none;
				  background-color: transparent;
				  border-radius: 0;	
				  color: #fff;
				  @include 	placecolor(#fff);

				  &:focus::-webkit-input-placeholder {
				  	color: transparent;
				  }

				  &::focus::-moz-placeholder {
				  	color: transparent;
				  }
				  :focus:-moz-placeholder {
				  	color: transparent;
				  }
				  :focus:-ms-input-placeholder {
				  	color: transparent;
				  }
				}
			&.inp-user:before{
				@include sprite($user-blue);
			}
			&.inp-phone:before{
				@include sprite($phone-empty);
			}
			
		}
}

.poup-mouse-out{
	width: 100%;
	max-width: 1040px;

	p{
		margin-bottom: 25px;
	}

	label{
		margin-bottom: 12px;
		input{
			@include placecolor(rgba(#000, .7));

			&:focus::-webkit-input-placeholder {
		  	color: transparent !important	;
		  }

		  &::focus::-moz-placeholder {
		  	color: transparent !important	;
		  }
		  &::focus:-moz-placeholder {
		  	color: transparent !important	;
		  }
		  &::focus:-ms-input-placeholder {
		  	color: transparent !important	;
		  }
		}
		&:last-of-type{
			margin-bottom: 30px;	
		}
	}
	.button{
		width: 100%;	
	}

	.form-bottom{
		label{
			margin-bottom: 0;
		}
	}

}
.poup-mouse-out__left,
.poup-mouse-out__right{
	flex-grow: 1;
	flex-shrink: 0;
	width: 50%;
}
.poup-mouse-out__left{
	img{
	max-width: 100%;
		
	}
}
// ik select
.ik_select_list_inner{
	background-color: #F9F8F8;

	ul{
		li{
			list-style-type: none;
			cursor: pointer;
		}
	}
}
.ik_select_link{
	position: relative;
	width: 219px;
	padding: 13px 60px 13px 27px;
	border: 1px solid #CCCCCC;
	border-radius: 5px;
	white-space: nowrap;
	color: #8f8f8f;
	&:after{
		content: '';
		@include 	absolute-center;
		left: auto;	
		right: 22px;	
		width: 16px;
		height: 12px;	
		background: 	 url(../img/icons/select-arrow.png) 0 0 no-repeat;
	} 
}
.ik_select{
	position: relative;	
		cursor: pointer;	
	
}
.ik_select_option{
		padding: 	5px 60px 5px 27px;
}
// end ik select

// magnific
button.mfp-close{
	top: 7px;
	background:  url(../img/icons/popup-close.png) left center no-repeat;

	&:hover, &:active{
		 background-image: url(	../img/icons/popup-close-hover.png);
	}
	
}
.mfp-close-btn-in .mfp-close{
	color: transparent;
	
}
// end magnific