.header-bottom--assortment{
	padding-bottom: 0;
}
.title-wrapper--assortment{
	@include col();
	@include col-9();
	@include col-sm-12();
	position: relative;
	z-index: 1;
	text-align: left;

	h1{
		margin-bottom: 50px;
		text-align: left;
		letter-spacing: -.1px;

		&.l-spacing{
		
		font-weight: 700;
		letter-spacing: -1.5px;
		}
		
	}

	.button{
		margin-bottom: 30px;
		&:before{
			display: none;
		}
	}
}
.header-bottom{
	.title-wrapper--assortment{
		p{
			max-width: 586px;
			margin-bottom: 50px;
			margin-left: 0;
			margin-right: 0;
		text-align: left;
			font-family: $rob-r;
			color: #4e4d4d;
		}
		
		.assortment-note{
			max-width: 386px;
			font-size: 12px;
			// line-height: 10px;
			color: rgba(#393939, .8);

		}
		
	}
}
.assortment-img-cont{
	position: relative;
	// overflow: hidden;
}
.assortment-page-visual{
	position: absolute;
	bottom: -16px;
	max-width: 750px;	
	right: 0px;
}
.assortment-pointer{
	@include absolute-center;
	top: auto;
	bottom: 44px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	width: 99px;
	z-index: 5;
	cursor: pointer;

	span{
		font-size: 13px;
		color: #fff;
	}
}
.assortment-pointer__left{
	width: 14px;
	height: 43px;
	margin-right: 15px;
	border: 1px solid #fff;
	border-radius: 7px;
	position: relative;
}
.assortment-pointer__point{
	@include absolute-center;
	bottom: auto;
	top: 3px;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background-color: #fff;
	transition: transform 1s;
	animation: shuttle 1s infinite linear;
}

@keyframes shuttle {
	0% { 
		transform: translateY(0);
		
	 }
	100% {
		
	transform: translateY(30px);
	  }
}
.assortment-content{
	padding-top: 60px;
	h2{
		span{
			display: block;
			font-size: 32px;
			// margin-top: 20px;
		}
	}
}
.assortment-page-item{
	@include col();
	@include col-12();
	margin-bottom: 20px;
}
.assortment-page-item__header{
	padding: 10px 10px;
	text-align: left;
	font-size: 18px;
	color: #fff;
	background-color: $text-blue;
	border: 	1px solid #B1B1B1;
	border-bottom: 	none;
}
.assortment-page-item__body{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;

	height: 314px;
	
}
.assortment-thumb{
	width: 378px;
	flex-shrink: 0;
	background:   center no-repeat;
	background-size: cover;	
	border-top: 	1px solid #B1B1B1;
	img{
		max-width: 100%;
	}
}

.assortment-usage{
	width: 270px;
	flex-shrink: 0;
	overflow: auto;
	font-size: 12px;
	line-height: 16px;
	border-bottom: 	1px solid #B1B1B1;
	border-right: 	1px solid #B1B1B1;
}

.assortment-usage__bottom{
		padding: 15px;
		padding-bottom: 0;
		p{
			margin: 0;
			text-align: left;
		}
}
.assortment-usage__top{
	height: 51.5px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #F9F9F9;
	border-bottom: 1px solid #B1B1B1;
	font-family: $rob-r;
}
.assortment__table-wrap{
		overflow: auto;	
		font-size: 12px;
	line-height: 16px;
	table{
		text-align: center;	
		border-collapse: collapse;
		th, td{
			padding: 4px;
			border: 1px solid #B1B1B1;
		}
		th{
			height: 51px;
			border-top: none;
			font-weight: 100;
			font-family: $rob-r;
			background-color: #F9F9F9;
			line-height: 14px;
		}
		td{
			padding: 12px 8px;
		}

		tr{
			th:first-of-type{
				border-left: none;
			}
			td:first-of-type{
				border-left: none;
			}
		}
	}
}
.icon-vibrotel{
	display: block;
	@include sprite($stat-vibro);
}