.subscribe-block{
		.container{
			justify-content: center;
		}
	
}
.title-wrapper--subscribe{
	margin-bottom: 20px;
	h1{
		margin-bottom: 0;
		font-family: $rob-b;
		font-size: 24px;
		line-height: 27px;
		text-transform: none;

	}
	p{
		font-family: $rob-r;
		font-size: 18px;
		color: #575757;
	}
}
.header-bottom--manual{
	.title-wrapper--subscribe{
		p{
			text-align: center;

		}
	}

}
.subscribe-block__inner{
	@include col();
	@include col-10();
	@include col-xs-12();

	padding: 35px 0;
	margin-bottom: 74px;
	background-color: $text-blue;
	box-shadow: 0px 6px 12px -3px rgba(0,0,0,0.65);

	h2{
		margin-bottom: 34px;
		
	}
}
.subscribe-block__top{
	padding: 0 7%;
	color: #fff;

	p{
		margin-bottom: 25px;
		line-height: 20px;
	}

	ul{
		margin-bottom: 20px;
		line-height: 20px;
		font-family: $rob-b;

		li{
			margin-left: 15px;
		}
	}
}
.subscribe-block__form{
	text-align: center;
	label{
		width: 275px;
		margin: 0 2px;
		display: inline-block;
		vertical-align: middle;
	
		&.inp-mail:before{
			@include sprite($mail);
		}

	}
	input{
		display: block;
		width: 100%;
		// padding-top: 25px;
		// padding-bottom: 25px;

		@include placecolor(rgba(0, 0, 0, .7));
	}
	.button{
		width: 275px;
		vertical-align: middle;
	}
}

.checkbox-custom{
	display: inline-block;
	position: relative;
	width: 18px;
	height: 18px;
	margin-right: 20px;
	border-radius: 3px;
	border: 3px solid rgba(#43608a, .4);

	&:before{
		content: '';
		display: none;
		@include absolute-center;
		@include sprite($cust-check);
	}

}

.form-bottom{
	margin-top: 15px;
	text-align: center;

	label{
		width: auto;
		input{
			// position: absolute;
			// left: -9999px;
			left: 0;	
			opacity: 0;
			width: auto;
			display: inline-block;	

			&:checked + span:before{
				display: block;
			}

		}

		span{
			display: inline-block;
			vertical-align: middle;
			color: #787878;

			a{
				color: #787878;
			}

		}

	}
}

						