.quality{
	padding-top: 74px;
	padding-bottom: 50px;
	background: url(../img/bg-mosaic.jpg) center top no-repeat;
	background-size: cover;
	overflow: hidden;
	.title-wrapper{
		@include col();
		@include col-12();
		// @include col-sm-12();
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;

		h2{
			text-align: left;
		}


	}
}
.quality-logo{
	width: 260px;
	margin-right: 30px;
	flex-shrink: 0;
	background:  url(../img/logo-wht.png) left top no-repeat;
	background-size: 100%;
}
.quality-item{
	@include col();
	@include col-3();
	@include col-lg-4();
	@include col-sm-12();
	position: relative;
	padding-right: 20px;

	p{
		font-size: 13px;
		color: #dddddd;
		line-height: 17px;
	}
	&:first-child{
		top: 99px;
	}
	&:nth-child(2){
		top: 42px;
	}
}
.quality-item__star{
	width: 27px;
	height: 27px;
	margin-bottom: 16px;
	background:  url(../img/icons-svg/star.svg) 0 0 no-repeat;
	background-size: 100%;
}
.quality-item__title{
	position: relative;
	padding-bottom: 15px;
	margin-bottom: 15px;
	font-family: $be-b;
	font-size: 24px;
	line-height: 23px;
	color: #fff;
	text-transform: uppercase;

	
	&:after{
		content: '';
		display: block;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 60%;
		height: 1px;
		background-color: #D7D7D7;
	}
}
.quality-illustration{
	@include col();
	@include col-9();
	position: relative;

}
.quality-illustration__outer{
	position: absolute;
	left: -118px;
	top: 50px;
}
.quality-illustration__zoom{
	position: absolute;
	left: 100px;
	top: 150px;
	width: 267px;
	height: 267px;
	border-radius: 50%;
	// outline: 1px solid red;
	// background-color: #959595;
	// overflow: hidden;
	background: #fff url(../img/plint-hidden.png) -380px -50px no-repeat;
	animation: prof 3s ease-out infinite alternate;

	// img{
	// 	position: absolute;
	// 	top: -680px;
	// 	left: 40px;
	// 	display: block;
	// }
}
@keyframes prof {
  0% {
    background-position: -380px -50px;
}
  25% {
    background-position: -390px -50px;
}
 50% {
    background-position: -380px -70px;
}
75% {
    background-position: -400px -70px;
}

}
.quality-sertification{
	@include col();
	@include col-3();
	@include col-lg-12();
	margin-top: -70px;
	text-align: center;
	
	p{
		margin-top: 0;
		margin-bottom: 17px;
		line-height: 18px;
		
		text-align: 	left;
		 pointer-events:none !important;
  // color:#fff !important;
  cursor:default !important;
  text-decoration: none !important;
  -webkit-text-fill-color: rgba(255, 255, 255, 1); 
   -webkit-opacity: 1; 
   color: rgba(255, 255, 255, 1); 
	}
	.button{
		margin-top: 20px;
		width: 100%;
	}
}
.quality-sertification__gost{
	margin-top: 25px;
	margin-bottom: 22px;
	font-family: $be-b;
	font-size: 26px;
	color: rgba(#fff, .5);
	// text-transform: uppercase;
}
.quality-sertification__num{
	margin-bottom: 58px;
	font-size: 20px;
	color: rgba(#fff, .2);
}